<style lang="scss" scoped>
.dialog_pram {
  margin-bottom: 10px;
  display: block;
}
</style>

<template>
  <div class="app-container">
    <el-dialog
        title="节点管理"
        :visible.sync="addShow"
        width="600px"
        :close-on-click-modal='false'
        >
        <el-form ref="ruleForm" :model="editData" :rules="rules" label-width="100px">
            <el-form-item label="节点名称" prop="nodeName">
                <el-input clearable  class="dialog_pram" placeholder="请输入节点名称" v-model="editData.nodeName"  />
            </el-form-item>
            <el-form-item label="节点地址" prop="nodeAddress">
                <el-input clearable class="dialog_pram" placeholder="请输入节点地址" v-model="editData.nodeAddress"  />
            </el-form-item>
            <el-form-item label="节点状态" prop="nodeType">
                 <el-radio v-model="editData.nodeStatus" :label="1">启用</el-radio>
                 <el-radio v-model="editData.nodeStatus" :label="0">禁用</el-radio>
            </el-form-item>
            <el-form-item label="节点类型" prop="nodeType">
                <el-radio v-model="editData.nodeType" :label="1">微演示</el-radio>
                <el-radio v-model="editData.nodeType" :label="2">iSpring</el-radio>
                <el-radio v-model="editData.nodeType" :label="3">SDK</el-radio>
            </el-form-item>
            <el-form-item label="节点权重" prop="nodeWeight">
                <el-input-number  class="dialog_pram" placeholder="请输入节点权重" v-model="editData.nodeWeight" />
            </el-form-item>
        </el-form>   
        <span slot="footer">
            <el-button  type="primary"  @click="submitUpload">确定</el-button>
            <el-button  type=""  @click="cancelUpload">取消</el-button>
        </span>
    </el-dialog>
    <div class="filter-container">
        <el-button class="filter-item" type="primary" icon="el-icon-edit" @click="updateData({nodeWeight:0,nodeStatus:1,nodeType:1})">新增节点</el-button>
    </div>

    <el-table
      v-loading="listLoading"
      :data="dataList"
      :stripe="true"
      border
      fit
      highlight-current-row
      style="width: 100%;"
    >
      <el-table-column  label="节点名称" prop="nodeName" align="center" />
      <el-table-column  label="节点地址" prop="nodeAddress" align="center">
        <template slot-scope="scope">
          <el-tag type="info">{{ scope.row.nodeAddress }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column  label="节点类型" prop="nodeType" align="center" >
            <template slot-scope="scope">
                {{scope.row.nodeType==1?'微演示':scope.row.nodeType==2?'iSpring':'SDK'}}
            </template>
      </el-table-column>
      <el-table-column  label="节点状态" prop="nodeStatus" align="center" >
          <template slot-scope="scope">
          <el-tag :type="scope.row.nodeStatus==0?'warning':'success'">{{ scope.row.nodeStatus==0?'禁用':'启用' }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column  label="节点权重" prop="nodeWeight" align="center" />
      <el-table-column width="150" label="操作" prop="" align="center" >
        <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="updateData(scope.row)">修改</el-button>
            <el-button type="warning" size="mini" @click="deleteData(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { Transform } from "@/api/transform";
export default {
  name: "nodeControl",
  data() {
    return {
      addShow: false, //是否显示弹出框
      // 编辑参数
      editData: {
        nodeAddress:'',
        nodeName:'',
        nodeStatus:0, //0禁用  1启用
        nodeType:1, //1微演示  2    3:sdk
        nodeWeight:0,
      },
      listLoading: false,
      dataList: [],
      currentId: "",
      rules: {
        nodeAddress: [
          { required: true, message: "节点地址", trigger: "change" }
        ],
        nodeName: [
          { required: true, message: "节点名称", trigger: "change" }
        ],
        nodeStatus: [
          { required: true, message: "节点状态", trigger: "change" }
        ],
        nodeType: [
          { required: true, message: "节点类型", trigger: "change" }
        ],
        nodeWeight: [
          { required: true, message: "节点权重", trigger: "change" }
        ],
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**
     * @description 获取初始化数据
     * @function getList
     * @return {type} {description}
     */
    getList() {
      this.listLoading = true;
      Transform.getNodeList().then(response => {
        this.dataList = response.data;
        this.listLoading = false;
      });
    },
    /**
     * @description 新增或者更新数据
     * @function {updateData
     * @return {type} {description}
     */
    updateData(row) {
      this.currentId = row.id || null;
      this.addShow = true;
      this.editData = {
        nodeAddress:row.nodeAddress,
        nodeName:row.nodeName,
        nodeStatus:row.nodeStatus, //0禁用  1启用
        nodeType:row.nodeType, //1微演示  2    3:sdk
        nodeWeight:row.nodeWeight,
      };
    },
    /**
     * @function submitUpload
     * @return {type} {description}
     */
    submitUpload() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          if (!this.currentId) {
            Transform.createNodeList(this.editData).then(res => {
              this.$message({
                type: "success",
                message: "成功新增"
              });
              this.getList();
              this.addShow=false;
            });
          } else {
            Transform.putNodeList({
                id:this.currentId,
                ...this.editData
            }).then(res => {
              this.$message({
                type: "success",
                message: "成功更新"
              });
              this.getList();
              this.addShow=false;
            });
          }
        } else {
          return false;
        }
      });
    },
    cancelUpload() {
      this.addShow = false;
    },
    /**
     * @description 删除版本
     * @function deleteData
     * @return {type} {description}
     */
    deleteData(id) {
      this.$confirm("确定删除该节点?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Transform.deleteNodeList({
            nodeId:id,
          }).then(res => {
            this.$message({
              type: "success",
              message: "成功删除"
            });
            this.getList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>
